$primary-color: whitesmoke;
$secondary-color: black;
$accent-color: #6e6e6e;

body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background: $secondary-color;
}

a {
  text-decoration: none;
}

.nav-bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  align-items: center;
  position: relative;
  min-width: 100%;
  max-width: 100%;
  background: $primary-color;

  // #{&}__title{
  //   display: flex;
  //   justify-content: center;
  //   flex: auto;
  // }

  .page-name {
    /*SORTING ALGORITHMS*/
    display: block;
    padding: 1em;
    overflow: auto;
    color: $secondary-color;
    font-size: 1.25rem;
    font-weight: bold;
    letter-spacing: 1px;
  }
  .nav-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
}

/*#region Options Selector*/
.option-wrap, .modes-wrap{
  position: relative;
  display: block;
  // min-width: 15ch;
  margin-left: 1ch;
  margin-right: 1ch;
}
.select-items, .mode-items {
  position: absolute;
  color: $primary-color;
  background-color: $secondary-color;
  right: 0;
  left: 0;
  border: 0.1em solid $primary-color;
  // direction: rtl;
  // overflow-y: auto;
  // overflow-x: visible;
  // max-height: 50vh;
}
.hide-items {
  display: none;
}
.current-select, .cur-mode {
  font-weight: bold;
  font-size: initial;

  padding: 0.4em 2.75vw 0.4em 0.4em;
  min-width: 15ch;
  border: 0.25em solid $secondary-color;
  border-radius: 5px;

  cursor: pointer;
  outline: none;
  transition: background 300ms ease-in, color 300ms ease-out;

  &:hover, .active{
    background: $secondary-color;
    color: $primary-color;
  }
  &:focus{
    background: $secondary-color;
    color: $primary-color;
    box-shadow: 0 0 6px 1px gray;
  }
}

.sub-arrow-down, .arrow-down{
  position: absolute;
  font-size: 1.5rem;
  right: 0.5ch;
  top: 21.17463%;
}
.arrow-down {
  transition: transform 100ms ease-in;
}
.choices, .m-choices {
  font-weight: bold;
  text-align: center;
  padding: 1vh 0;
  transition: background 100ms ease-out, font-size 100ms ease-out;
  cursor: pointer;
  &:hover {
    background: $accent-color;
    /* background: $primary-color; */
    /* color:$secondary-color; */
    font-size: 1.2em;
  }
}
.sub-select {
  position: relative;
  #{&}__front {
    display: block;
  }
  #{&}__items {
    background: $secondary-color;
    border: 0.1em solid $primary-color;
    right: -100%;
    left: 100%;
    top: 0;
    /* transform: translate(100%, 0); */
    position: absolute;
  }
}
/*#endregion */

/*#region Amount Input*/
/*AMMOUNT NAME*/
#ip-name, #modes-title{
  padding: 1vh 0;
  padding-left: 1vw;
  font-weight: bold;
  margin: 0.5em 0px;
}
.ip {
  /*AMOUNT*/
  width: 6vw;
  min-width: 8ch;

  padding: 1vh 1vw;
  margin: 0.5em;
  margin-left: 1ch;

  border: 0.25em solid $secondary-color;
  border-radius: 5px;

  background: $primary-color;

  color: $secondary-color;
  font-size: initial;
  font-weight: bold;

  outline: none;
  transition: background 300ms ease-out, color 300ms ease-out;

  &:hover{
    background: $secondary-color;
    color: $primary-color;
  }
  &:focus{
    background: $secondary-color;
    color: $primary-color;
    box-shadow: 0 0 6px 1px gray;
  }
}
/*#endregion */

/*#region Buttons*/
.btn {
  background: $primary-color;
  border: none;

  font-size: initial;
  text-align: center;
  padding: 1vh 1vw;
  min-width: 1.5em;
  margin: 0.3em;
  display: block;
  cursor: pointer;

  transition: background 100ms ease-out, color 100ms ease-in,
    box-shadow 100ms ease-out, transform 20ms ease-out;
  
  &:hover{
    background: $secondary-color;
    color: $primary-color;
    box-shadow: 0 0 0 4px $secondary-color;
  }
  &:active{
    transform: scale(1.1);
  }
  &:focus{
    outline: none;
    box-shadow: 0 0 0 4px $secondary-color;
  }
}
/*#endregion*/

/*#region Canvas */
.main-canvas {
  border-top: 2px solid $secondary-color;
  background: $secondary-color;
  /* width: 100%;
	height: 100%; */
}
/*#endregion */
